.menu {
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.menu .menuTitle {
  /* font-family: cursive; */
  font-size: 40px;
}

.menuList {
  width: 70vw;
  height: auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  place-items: center;
}
.menuList h1 {
  font-size: 20px;
}

/* MENU ITEM STYLING */

.menuItem {
  border-radius: 15px;
  width: 300px;
  height: 350px;
  margin: 20px;
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
}
.menuItem:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in;
  cursor: pointer;
}
.menuItem button:hover {
  box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
  transition: 0.3s ease-in;
  cursor: pointer;
  background-color: orange;
}

.menuItem button {
  border-radius: 5px;
  margin-left: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  justify-content: center;
  background-color: #121619;
  height: 30px;
  width: 100px;
  color: white;
  text-decoration: none;
  font-size: 20px;
}
.menuItem div {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  width: 100%;
  height: 200px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.menuItem h1,
.menuItem p {
  margin-left: 20px;
  margin-top: 5px;
}

@media only screen and (max-width: 1300px) {
  .menuList {
    grid-template-columns: 1fr 1fr;
  }
}

@media only screen and (max-width: 800px) {
  .menuList {
    grid-template-columns: 1fr;
  }
}
@media only screen and (max-width: 400px) {
  .menuItem {
    border-radius: 15px;
    width: 250px;
    height: 300px;
    margin-left: 0px;
    margin-top: 12px;
    margin-bottom: 12px;
  }
  .menuList h1 {
    font-size: 13px;
  }
  .menuItem button {
    border: none;
    cursor: pointer;
    justify-content: center;
    height: 25px;
    width: 80px;
    color: white;
    text-decoration: none;
    font-size: 13px;
  }
}
@media only screen and (min-width: 300px) {
  .menu .menuTitle {
    font-size: 30px;
    margin-left: 30px;
  }
}
