.footer {
  width: 100%;
  height: 200px;
  background-color: #121619;
  /* display: flex; */
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 10px;
}

.socialMedia svg {
  color: white;
  margin: 1px;
  font-size: 25px;
  cursor: pointer;
}

.footer p {
  color: white;
  text-align: center;
}
@media only screen and (min-width: 300px) {
  .footer {
    width: 100%;
  }
}
