/* @import url("https://fonts.googleapis.com/css2?family=Karantina:wght@700&display=swap"); */

/*-----Variable Css----*/
:root {
  --header-height: 3rem;
  --font-semi: 600;
}
/*-----Colores----*/
:root {
  --first-color: #ff4500;
  --second-color: #080808;
  color: rgb(34, 47, 92);
}

/*-----Fuente y tipografia----*/
:root {
  --body-font: 'poppins', sans-serif;
  --big-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --normal-font-size: 0.938rem;
}
@media screen and (min-width: 368px) {
  :root {
    --big-font-size: 3.5rem;
    --h2-font-size: 2rem;
    --normal-font-size: 1rem;
  }
}

/*-----Margin-----*/
:root {
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 5rem;
}

/*-----z Index-----*/
:root {
  --z-back: -10;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
}

/*-----Base-----*/
*,
::before,
::after {
  box-sizing: border-box;
}

/*-----About-----*/
.about {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.aboutTop {
  width: 100%;
  height: 45%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.aboutBottom {
  display: flex;
  flex-direction: column;
  height: 55%;
  align-items: center;
  margin-top: 50px;
}

.aboutBottom p {
  width: 1000px;
  justify-content: center;
}

.about .aboutBottom h1 {
  font-weight: 400;
  font-size: 30px;
  color: black;
  height: 10px;
  display: flex;
  justify-content: center;
  transform: translateY(-40px);
}

.skills__container {
  row-gap: 2rem;
  text-align: center;
}
.skills__subtitle {
  margin-bottom: var(--mb-2);
}
.skills__text {
  margin-bottom: var(--mb-4);
}

.skills__data {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  font-weight: var(--mb-4);
  padding: 0.5rem 1rem;
  margin-bottom: var(--mb-4);
  border-radius: 0.5rem;
  box-shadow: 0 4px 25px rgba(14, 36, 49, 0.15);
}
.skills__icon {
  font-size: 2rem;
  margin-right: var(--mb-2);
  color: var(--first-color);
}
.skills__names {
  display: flex;
  align-items: center;
}
.skills__bar {
  position: absolute;
  left: 0;
  bottom: 0;
  background-color: var(--first-color);
  height: 0.25rem;
  border-radius: 0.5rem;
  z-index: var(--z-back);
}
.skills__html {
  width: 70%;
}
.skills__css {
  width: 70%;
}
.skills__js {
  width: 60%;
}
.skills__react {
  width: 60%;
}
.skills__node {
  width: 50%;
}

.skills__img {
  border-radius: 0.5rem;
}
.about__img {
  justify-self: center;
  margin-top: 10px;
}
.about__img img {
  width: 200px;
  border-radius: 100px;
  border: solid orange;
}

@media only screen and (max-width: 600px) {
  .about .aboutBottom h1 {
    height: 10px;
    display: flex;
    justify-content: center;
  }

  .navbar .rightSide {
    justify-content: flex-end;
    padding-right: 50px;
  }

  .navbar .rightSide button {
    display: inherit;
  }
}
